:root {
  --yellow: #FDE46A;
  --lightYellow: #FDE986;
  --davysGrey: #595959;
  --wheat: #E1CA96;
  --red: #CB2F2A;
  --blueish: #2E3860;
}

@font-face {
  font-family: finkHeavy;
  src: url(./assets/FinkHeavy\ Regular.ttf);
  font-weight: bold;
}

* {
  font-family: finkHeavy;
}

body {
  background-color: #FDFDEB;
  text-align: center;
  height: 100vh;
}

header {
  margin: 0 20px 20px 20px;
  display: flex;
  justify-content: space-around;
}

h1 {
  font-size: 2.5em;
  margin: 0;
}

footer {
  background-color: var(--yellow);
  box-shadow: 0 -10px 15px var(--lightYellow);
  font-size: 1.5em;
  display: flex;
  justify-content: space-between;
  flex-shrink: 0;
}

footer p {
  margin: 10px;
}

.App {
  text-align: center;
  margin: 0;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.no-animals {
  width: 100vw;
}

.fab {
  padding: 10px;
  color: black;
}

.title {
  background-color: var(--yellow);
  box-shadow: 0 10px 10px var(--lightYellow);
}

.header-hr {
  border: 0;
  height: 2px;
  background-color: black;
  margin: 0;
}

.modal-hr {
  border: 0;
  height: 1px;
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
}

.bells-img {
  position: relative;
  top: 6px;
  max-width: 40px;
  max-height: 40px;
}

.search {
  width: 100%;
  position: relative;
  display: flex;
}

.favorite {
  border-radius: 16px;
  background-color: var(--yellow);
  color: #834B1C;
  padding: 0 11px;
  font-size: 1.5em;
  border: 1px solid black;
}

.searchTerm {
  width: 100%;
  font-size: 1.8em;
  border: 3px solid #C52D28;
  border-right: none;
  padding: 5px;
  height: 20px;
  border-radius: 5px 0 0 5px;
  outline: none;
  color: #834B1C;
  font-weight: bold;
}

.searchButton {
  width: 40px;
  height: 36px;
  border: 1px solid #C52D28;
  background: #C52D28;
  text-align: center;
  color: #fff;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  font-size: 20px;
}

.wrap{
  width: 71.5vw;
  text-align: center;
  transform: translate(20%, 19%);
}

.tabs {
  font-size: 2em;
  font-weight: bold;
  color: var(--yellow);
  background-color: #C52D28;
  width: 33vw;
  height: 5vh;
  border: 1px solid black;
  border-radius: 0 0 10px 10px;
}

.tabs:hover {
  height: 6vh;
  cursor: pointer;
  background-color: rgb(177, 13, 7);
}

.tab-bkg {
  background-color: rgb(177, 13, 7);
  height: 6vh;
}

.animal {
  font-size: 2em;
  color: #834B1C;
  display: flex;
  flex-direction: column;
  align-content: center;
  background-color: var(--yellow);
  width: 40vw;
  max-width: 40vw;
  border: 1px solid black;
  border-radius: 15px;
  padding: 7px;
  margin: 5px;
  cursor: pointer;
}

.animal-name {
  margin: 0;
  font-size: 1.2em;
}

.price-list  {
  display: flex;
  justify-content: center;
  align-items: center;
  max-height: 1200px;
  margin-bottom: 25px;
  flex-wrap: wrap;
  overflow: auto;
  flex: 1 0 auto;
}

.prices {
  display: inline;
  margin: 0;
  font-weight: lighter;
}

.list-top {
  font-size: 1.2em;
  display: flex;
  justify-content: space-between;
  margin: 10px 0 17px 0;
}

.none {
  display: none;
}

.modal {
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  overflow: auto;
  width: 100%;
  height: 100%;
  background-color: rgb(0,0,0);
  background-color: rgba(0,0,0,0.4);
}

.modal-content {
  background-color: #FDFDEB;
  margin: 15% auto;
  padding: 20px;
  border: 1px solid black;
  border-radius: 20px;
  width: 75%;
  cursor: default;
}

@media only screen and (max-width: 500px) {
  .App { 
    font-size: .6em;
  }
}